.nav__sub-menu {
  position: relative;
  cursor: pointer;
}
.nav__sub-menu p {
  font-weight: 500;
  font-size: 14px;
  line-height: 120%;
  letter-spacing: 0.01em;
  text-transform: uppercase;
}
@media (max-width: 1280px) {
  .nav__sub-menu p {
    font-size: 13px;
  }
}
.nav__sub-menu svg {
  margin-left: 4px;
}
.nav__sub-menu ul {
  background: #fff;
  position: absolute;
  top: calc(100% + 28px);
  left: 0;
  width: 352px;
  overflow: hidden;
  will-change: transform;
  transition: transform 0.5s;
  transform: scaleY(0);
  transform-origin: 50% 0;
  z-index: 9;
  box-sizing: border-box;
}
@media (max-width: 991px) {
  .nav__sub-menu ul {
    position: relative;
    width: 100%;
    padding-left: 24px;
    height: 0;
  }
}
