  display: flex;
  justify-content: space-between;
  padding: 16px;
}
.nav__sub-menu ul.nav__sub-menu--show {
  will-change: transform;
  transition: transform 0.5s;
  transform: scaleY(1);
  transform-origin: 50% 0;
  z-index: 999;
}
@media (max-width: 991px) {
  .nav__sub-menu ul.nav__sub-menu--show {
    height: auto;
  }
}
.nav__sub-menu li {
  border-bottom: 1px solid rgba(51, 51, 51, 0.1);
}
.nav__sub-menu span {
  clip-path: polygon(25% 0, 100% 0%, 100% 100%, 25% 100%, 0 52%);
  padding: 3px 4px 2px 10px;
  background: #ff5900;
  font-weight: 700;
  font-size: 10px;
  line-height: 110%;
  color: #ffffff;
}

.search {
  position: absolute;
  right: 0;
  background: #ffffff;
  opacity: 0.8;
  width: 771px;
  padding: 23px 32px;
  will-change: transform;
  transition: transform 0.5s;
  transform: scaleY(0);
  transform-origin: 50% 0;
}
