.burger {
  overflow: hidden;
  display: none;
  height: 40px;
  min-width: 40px;
  overflow: hidden;
  padding: 12px 7.5px;
  width: 40px;
  background-color: transparent;
  z-index: 100;
  margin-left: 24px;
}
@media (max-width: 991px) {
  .burger {
    display: flex;
  }
}
.burger__line {
  width: 100%;
  height: 2px;
  transition: 0.3s;
  border-radius: 70px;
  background: #000;
}
.burger.open .burger__line:first-child {
  transform: translateY(7px) rotate(45deg);
}
.burger.open .burger__line:nth-child(2) {
  transform: translateX(calc(100% + 7.5px));
}
.burger.open .burger__line:last-child {
  transform: translateY(-7px) rotate(-45deg);
}

.menu-mobile {
  z-index: -1;
  opacity: 0;
  position: fixed;
  width: 100%;
  height: calc(100% - 109px);
  background: #fff;
}
.menu-mobile a,
.menu-mobile .nav__sub-menu--title {
  padding-top: 16px;
